import React, { Component } from "react";
import myfoto from "../images/myimage.jpeg";
import { AiFillGithub } from "react-icons/ai";
import { AiFillLinkedin } from "react-icons/ai";
import { FaFreeCodeCamp } from "react-icons/fa";
import { BsFileRichtext } from "react-icons/bs";
import CV from "../CV.docx"

export default class Main extends Component {
  render() {
    return (
      <div style={{ float: "left" }} className="container ">
        <img
          style={{ width: "250px" }}
          className="img-fluid img-thumbnail"
          src={myfoto}
          alt="..."
        />
        <h3 className="my-title">Ertuğrul Sarıhan</h3>
        <h5 className="pt-3 my-title">
          Hello! I'm am a Fullstack Developer.
        </h5>
        <div className="container">
          <a className="p-1" href="https://github.com/ertugrulsarihan">
            <AiFillGithub size="30px" />
          </a>
          <a className="p-1" href="https://www.linkedin.com/in/ertu%C4%9Frul-sarihan-11bb55189/">
            <AiFillLinkedin size="30px" />
          </a>
          <a className="p-1" href="https://www.freecodecamp.org/ertugruls">
            <FaFreeCodeCamp size="30px" />
          </a>
          <a className="p-1" href={CV} download><BsFileRichtext size="30px"/></a>
        </div>
      </div>
    );
  }
}
